.background-video {
    position: absolute;
    z-index: -1;
    width: 100%;
    height: 100%;
    opacity: 0.95;
    object-fit: cover;
  }

  .container-video{
    width: 100%;
    height: 100%;
  }

  .container-video img{
    display: block;
    width: 100%;
    height: 100%;
  }